@import 'fonts';
@import 'variables';

%transition {
  -webkit-transition: .3s ease;
  -o-transition: .3s ease;
  transition: .3s ease;
}

%transition_slow {
  -webkit-transition: 1s ease;
  -o-transition: 1s ease;
  transition: 1s ease;
}

%psevdo {
  content: '';
  display: inline-block;
  position: absolute;
}

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  outline: 0;
  border: 0;
}

body {
  color: $color;
  font-family: 'PFAgoraSansPro-Light';
  line-height: 1.3;
  font-size: 18px;
  letter-spacing: 0.25em;
  box-sizing: border-box;
  position: relative;
}

body * {
  box-sizing: border-box;
}

b, strong {
  font-family: $font-b;
}

button,
select {
  cursor: pointer;
  @extend %transition;
}

input,
textarea,
select,
button {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  color: #3D3D3D;
}

input[type="text"],
input[type="email"],
input[type="password"],
input[type="phone"],
input[type="date"],
input[type="submit"],
textarea {
  color: $color;
  font-family: 'PFAgoraSansPro-Light';
  @extend %transition;
}

img {
  max-width: 100%;
}

a {
  @extend %transition;
  color: $color;
  text-decoration: none;
  cursor: pointer;

  &.active {
    color: $blue;
    text-decoration: none;
  }

  &:hover {
    color: $blue;
    text-decoration: none;
  }
}

h1 {
  font-size: 46px;
  letter-spacing: 0.35em;
  text-transform: uppercase;
  font-weight: 300;
}

h2 {
  font-size: 36px;
  letter-spacing: 0.35em;
  text-transform: uppercase;
  font-weight: 300;
  margin-bottom: 38px;
}

.blue {
  color: $blue;
}

.flex-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}

.flex-box.flex-center {
  align-items: center;
}

.container {
  padding: 10px;
  margin: 0 auto;
  position: relative;
  animation: width_bg 2s forwards;
}

.hello-screen {
  margin: 0;
  background: $bgcolor url(../img/body-bg.svg) right top no-repeat;
  background-size: contain;
  h1 {
    position: relative;
    &.line_right_out:before {
      animation:line_right_out 2s forwards;
      }
    &:before {
      @extend %psevdo;
      width: 1114px;
      height: 1px;
      background: $color;
      bottom: -5px;
      right: 20px;
      animation: line_right 2s forwards;
      animation-delay: 4s;
      opacity: 0;
    }
  }
}

@keyframes width_bg {
  0% {
    margin: 0;
    width: 100%;
    height: 100%;
  }
  100% {
    width: calc(100% - 64px);
    height: calc(100% - 64px);
    margin: 32px;
  }
}

@keyframes line_right {
  0% {
    width: 200px;
    right: -50px;
    opacity: 1;
  }
  50% {
    width: 0px;
    right: 200px;
    opacity: 1;
  }
  100% {
    width: 1114px;
    right: 20px;
    opacity: 1;
  }
}

@keyframes line_right_out {
  100% {
    width: 200px;
    right: -50px;
    opacity: 1;
  }
  50% {
    width: 0px;
    right: 200px;
    opacity: 1;
  }
  0% {
    width: 1114px;
    right: 20px;
    opacity: 1;
  }
}

.header-logo {
  text-align: right;
  padding: 114px 114px 180px 0;

  p {
    span {
      display: block;
    }
  }
}

.wrapper-1100 {
  width: 1100px;
  text-align: right;
}

.wrapper-1500 {
  max-width: 1500px;
  margin: auto;
}

.enter-form {
  margin-top: 74px;
  padding-right: 10px;

  .form-row {
    margin-top: 40px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  .error-message {
    margin-right: 90px;
    display: none;
  }
  .flex-box {
    justify-content: flex-end;
    align-items: center;
    position: relative;

    &.blue_line_out:before {
      animation: blue_line_out 2s forwards;
    }
    &:before {
      @extend %psevdo;
      width: 677px;
      height: 1px;
      background: $blue;
      bottom: -5px;
      right: 10px;
      animation: blue_line 2s forwards;
      animation-delay: 5s;
      opacity: 0;
    }
  }

  p {
    margin-right: 54px;
  }

  input[type=text] {
    background: transparent;
    border: none;
    outline: none;
    font-size: 31px;
    letter-spacing: 1em;
    text-align: right;
    width: 175px;
  }

  input[type=submit] {
    color: #f6f6f67a;;
    background: transparent;
    border: none;
    outline: none;
    font-size: 18px;
    text-transform: uppercase;
    letter-spacing: .25em;
    text-align: right;
    margin-right: 5px;
    opacity: .5;

    &.active {
      color: $color;
      cursor: pointer;

      &:hover {
        color: $blue;
      }
    }
  }
}

@keyframes blue_line {
  0% {
    width: 200px;
    right: 500px;
    opacity: 1;
  }
  50% {
    width: 0px;
    right: 100px;
    opacity: 1;
  }
  100% {
    width: 677px;
    right: 10px;
    opacity: 1;
  }
}

@keyframes blue_line_out {
  100% {
    width: 200px;
    right: 500px;
    opacity: 1;
  }
  50% {
    width: 0px;
    right: 100px;
    opacity: 1;
  }
  0% {
    width: 677px;
    right: 10px;
    opacity: 1;
  }
}

.btn-link {
  color: $color;
  text-transform: uppercase;
  letter-spacing: .35em;

  &:hover {
    color: $blue;
  }
}

.inner-screen {
  background: $bgcolor;
  .header-logo {
    padding: 114px 114px 104px 0;
  }

  font-size: 16px;
  letter-spacing: .15em;

  .left-block {
    width: calc(100% - 400px);
    margin-right: 40px;
    min-width: 300px;

    p {
      margin-bottom: 34px;
    }

    .btn-link {
      margin-right: 64px;
      &.nomrg {margin-right: 0;}
    }
  }
}

.outline {
  position: relative;
  display: table;
  margin-bottom: 44px !important;
  z-index: 1;

  &.left:after {
    @extend %psevdo;
    z-index: -1;
    width: calc(100% + 25px);
    height: 0;
    top: -20px;
    left: -25px;
    border-left: 1px solid #006B9C;
    animation: border_left 1s forwards;
    animation-delay: 1s;
  }
  &.bottom:before {
    @extend %psevdo;
    z-index: -1;
    width: 0px;
    height: calc(100% + 40px);
    top: -20px;
    left: -25px;
    border-bottom: 1px solid #006B9C;
    animation: border_bottom 2s forwards;
    animation-delay: 1s;
  }

  &.outline-left:before {
    @extend %psevdo;
    z-index: -1;
    width: 0;
    height: 100%;
    top: -20px;
    left: -25px;
    border-left: 1px solid #006B9C;
    animation: border_left_large 2s forwards;
    animation-delay: 1s;
    border-bottom: 0;
  }

  &.outline-bottom:after {
    @extend %psevdo;
    z-index: -1;
    width: 300px;
    height: 0;
    bottom: -20px;
    left: 50px;
    border-bottom: 1px solid #006B9C;
    animation: border_bottom_large 2s forwards;
    animation-delay: 1s;
  }
}
@keyframes border_bottom_large {
  0% {
    width: 0;
  }
  0% {
    width: 50%;
  }
  100% {
    width: 300px;
  }
}
@keyframes border_left_large {
  0% {
    height: 0;
  }
  0% {
    height: 50%;
  }
  100% {
    height: 100%;
  }
}

@keyframes border_bottom {
  0% {
    width: 0;
  }
  0% {
    width: 50%;
  }
  100% {
    width: calc(100% + 25px);
  }
}
@keyframes border_left {
  0% {
    height: 0;
  }
  0% {
    height: 50%;
  }
  100% {
    height: calc(100% + 40px);
  }
}

.social {
  display: inline-block;
  width: 26px;
  height: 26px;
  margin-right: 30px;
  cursor: pointer;

  img {
    @extend %transition;
  }

  &:hover img {
    filter: brightness(0) invert(1);
  }
}

.right-block {
  width: 340px;

  .outline {
    margin-bottom: 0 !important;
  }

  p {
    margin-bottom: 34px;
  }
}


/*adaptive*/


@media (max-width: 1620px) {
  .container {
    padding: 30px 40px;
  }
}

@media (max-width: 1500px) {
  .wrapper-1100 {
    width: 900px;
  }
}

@media (max-width: 1350px) and (max-height: 700px) {
  .container {
    width: 100%;
    height: 100%;
    margin:0;
    animation:none;
  }
}

@media (max-width: 1200px) {
  .inner-screen .header-logo {
    padding: 40px 114px 104px 0;
  }
  .inner-screen p br {
    display: none;
  }
  .container {
    margin:0;
    animation:none;
  }
}

@media (max-width: 1000px) {
  .wrapper-1100 {
    width: 800px;
  }
}

@media (max-width: 900px) {
  .wrapper-1100 {
    width: 100%;
  }
  .header-logo {
    text-align: right;
    padding: 40px 10px 180px 0;
  }
  .inner-screen .header-logo {
    padding: 40px 10px 100px 0;
  }
  h2 {
    font-size: 30px;
  }
  .right-block {
    width: 300px;
  }
}

@media (max-width: 700px) {
  .header-logo {
    text-align: right;
    padding: 40px 10px 180px 0;
  }
  .hello-screen h1 {
    font-size: 46px;
    letter-spacing: 0.15em;

    &:before {
      right: 7px;
    }
  }
  body {
    letter-spacing: 0.15em;
  }
  .enter-form input[type=text] {
    letter-spacing: 0.25em;
  }
  .enter-form {
    padding: 0;
  }
  .inner-screen .left-block,
  .inner-screen .right-block {
    width: 100%;
    margin: 0;
  }
  .inner-screen .left-block {
    margin-bottom: 100px;
  }
  .outline.outline-bottom:before {
    left: -25px;
  }
}

@media (max-width: 580px) {
  .error-message br {
    display: none;
  }
  .enter-form p {
    margin-right: 0;
  }
  .enter-form .error-message {
    margin-right: 30px;
  }

}

@media (max-width: 400px) {
  .hello-screen h1 {
    font-size: 30px;
  }
  .enter-form p {
    font-size: 16px;
    margin-bottom: 10px;
  }
  .inner-screen .left-block .btn-link {
    margin-right: 30px;
  }
  .enter-form .error-message {
    font-size: 14px;
  }
}

@media (max-width: 370px) {
  .hello-screen h1 {
    font-size: 26px;
  }
  h2 {
    font-size: 20px;
  }
}

@media (max-width: 1024px) and (min-height: 1365px) {
  .header-logo {
    padding: 114px 114px 300px 0;
  }
}

@media (max-width: 375px) and (min-height: 670px) {
  .header-logo {
    padding-bottom: 100px;
  }
}

@media (max-width: 340px) and (max-height: 500px) {
  .header-logo {
    padding: 20px 10px 70px 0;
  }
  .header-logo img {
    width: 120px;
  }
}